/*
mixinをまとめています。
 */

@mixin ie_only {
  @media all and (-ms-high-contrast: none) {
    *::-ms-backdrop, & {
      @content;
    }
  }
}

@mixin link_color($color) {
  a, a:visited, a:hover, a:focus, a:active {
    color: $color;
    @content;
  }
}


@mixin before_text($content){
  &:before {
    content: $content;
    padding-right: 5px;
    display: inline;
  }
  a {
    display: inline;
  }
  @content;
}
