blockquote {
  position: relative;
  padding: 7px 16px;
  box-sizing: border-box;
  font-style: italic;
  color: #212121;
  border: solid 3px #212121;
  margin: 3rem 1rem;
  background-color: #F4F5F7; }

blockquote:before {
  display: inline-block;
  position: absolute;
  top: -20px;
  left: -20px;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  vertical-align: middle;
  text-align: center;
  content: "\f10d";
  font-family: FontAwesome;
  background: #212121;
  color: #F4F5F7;
  font-size: 22px; }

blockquote:after {
  display: inline-block;
  position: absolute;
  bottom: -20px;
  right: -20px;
  width: 40px;
  height: 40px;
  line-height: 40px;
